import { useState } from "react";
import "./styles.css";

const initialData = ["ux design", "ux types", "ux movies", "us set"];

export default function App() {
  const [data, setData] = useState(initialData);

  const [foundItem, setFoundItem] = useState("");

  const [inputValue, setInputValue] = useState("");

  const handleInputChange = (e) => {
    const { value } = e.target;
    setInputValue(value);

    const filteredData = initialData.filter((elem) =>
      elem.toLowerCase().startsWith(value.toLowerCase())
    );

    setData(filteredData);

    if (filteredData.length > 0 && value.length > 0) {
      setFoundItem(filteredData[0]);
    } else {
      setFoundItem("");
    }
  };

  return (
    <div className="App">
      <div>
        <div className="input-area">
          <input
            type="text"
            placeholder="type..."
            value={inputValue}
            onChange={handleInputChange}
          />
          <div className="ghost-input">{foundItem}</div>
        </div>
        <div className="data-wrapper">
          {data.map((data, index) => (
            <div key={index}>
              <p>{data}</p>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
}
